@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
:root {
    --primary-color: #007afb; 
    --accent-color: #f7a92d;
    --text-color: #121212;
    --overlay-color: rgba(3, 3, 3, 0.4);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Alata', sans-serif;
    width: 100%;
    height: 100%;
    scroll-behavior: smooth;
    background: #fefffe;
}

h2 {
    letter-spacing: 2px;
    -webkit-text-decoration: underline #f7a92d;
            text-decoration: underline #f7a92d;
    -webkit-text-decoration: underline var(--accent-color);
            text-decoration: underline var(--accent-color);
}

.overlay-color {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(3, 3, 3, 0.4);
    background-color: var(--overlay-color);
}

/* custom styled general button */
.custom-btn {
    padding: 10px;
    width: 200px;
    border: none;
    color: #fefffe;
    background: #007afb;
    background: var(--primary-color);
}

.custom-btn:hover {
    color: #fefffe;
    background: darkblue;
}
.hero-section {
    height: 700px;
    width: 100%;
    background: url(/static/media/hero2.1bc93665.jpg)  no-repeat center/cover;
    position: relative;
}

.hero-content {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: auto;
    padding: 6rem 1rem;
    max-width: 100%;
    line-height: 2em;
    color: #fefffe;
}

.hero-content h1 {
    font-size: 3rem;
    font-weight: 600;
    color: var(--accent-color);
    letter-spacing: 1px;
}

.hero-content p {
    font-size: 1.5rem;
}

.hero-button {
   padding: 10px;
   width: 200px;
   border: 1px solid #fefffe !important;
   color: #fefffe;
   text-align: center;
   background: transparent;
   text-decoration: none !important;
}

.hero-button:hover {
   border: none !important;
   color: #fefffe;
   background: var(--primary-color) !important;
}

.about_section {
    width: 100%;
    height: auto;
}

.about_section .about_content {
    padding: 2rem;
    margin: 1rem
}

.about_text h4 {
    padding: 5px;
    -webkit-text-decoration: underline var(--accent-color);
            text-decoration: underline var(--accent-color);
    font-size: 2.5rem;
}

.about_text h3 {
    font-size: 1.5rem;
    line-height: 3rem;
}

.about_img {
  width: 100%;  
}

.about_img img {
    width: 500px;
}

.about_text span {
   color: var(--primary-color);
}


@media screen and (max-width: 480px) {
    .hero-section {
        height: 85vh;
    }

    .hero-content{
        height: 100vh;
        margin-bottom: 2rem;
    }

    .hero-content h1 {
        font-size: 2rem;
    }   

    .hero-content p {
        font-size: 1rem;
    }

    .about_img img {
        width: 100%;
        height: 100%;
    }

    .about_section .about_content {
        padding: 1rem;
        margin: 1rem 0;
    }
    
    .about_text h4 {
        padding: 1rem;
        -webkit-text-decoration: underline var(--accent-color);
                text-decoration: underline var(--accent-color);
        font-size: 1.5rem;
    }
    
    .about_text h3 {
        font-size: 1.2rem;
        line-height: 2rem;
    }
}

@media screen and (max-width: 786px) {
    .about_img img {
        width: 100%;
        height: 100%;
    }   
}



.card_content span {
    color: #000;
}
.services-cards {
    padding: 30px;
}

.cards {
    box-shadow: 0 0 10px rgba(169, 169, 169, 0.1);
}

.cards .services_icon {
    font-size: 2rem;
}

.cards p {
    color: #696969;
    padding: 1rem;
}
.partners_section {
    background: var(--text-color);
    height: 450px;
    color: #fefffe;
}

.newsletter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 2rem;
    align-items: center;
}

.form {
    width: 100%;
}

.form h5 {
    padding: 0.8rem;
}

.form input {
    width: 65%;
    border: 1px solid var(--text-color);
    outline: none;
}

.form .newsletter_btn {
    width: 20%;
    background: var(--primary-color);
    color: #fefffe;
    letter-spacing: 2px;
    border: 1px solid var(--primary-color);
    outline: none;
}

.form input, .form .newsletter_btn {
    padding: 10px;
}

.brands {
    padding: 1rem;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin: auto;
}
.brand_img {
    width: 100%;
}

.brands img {
    width: 80%;
}

@media screen and (max-width: 480px) {
    .partners_section {
        height: 100%;
    }

    .form h5 {
        font-size: 1.2rem;
    }

    .form input, .form .newsletter_btn {
        padding: 5px;
        width: 100%;
    }

    .form .newsletter_btn {
        letter-spacing: 0;
        
    }

    .brands img {
        width: 50%;
    } 
}
.cargo-section {
    background: var(--primary-color);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    color: #fefffe;
    padding: 20px;
 }


.footer {
    background: #141618;
    color: #f3f6f8;
    height: 100%;
}

.footer_content {
    padding: 3rem 2rem;
}

.footer_content h4 {
    color: var(--accent-color);
    font-weight: bold;
    cursor: pointer;
}

ul {
    padding: 0.8rem 0;
}

li {
    list-style: none;
    cursor: pointer;
    padding: 0.5rem 0;
}

.quick_links .links {
    text-decoration: none !important;
    color: #f3f6f8 !important;
}
    

.newsletter-form {
    background: #d5d5d5;
    padding: 10px;
    border: none;
    border-radius: 5px;
    
}

.social-media {
    background: #121212;  
}

.social-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: 100%;
    padding: 15px;
}

@media screen and (max-width: 480px) {
    .footer-container {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        text-align: center;
    }

    .partners-section {
        margin-bottom: 15px;
    }

    .footer-contact {
        margin-bottom: 15px;
    }
}

@media screen and (max-width: 700px) {
    .footer-container {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        text-align: center;
    }

    .partners-section {
        margin-bottom: 15px;
    }

    .footer-contact {
        margin-bottom: 15px;
    } 
}
.card_decks {
   padding: 3rem;
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   justify-content: center;
   align-items: center;
}


.discover {
    height: 600px;
    width: 100%;
    background: url(/static/media/discover.cefbe7bd.jpg)  no-repeat center/cover;
    position: relative;
}

.discover_content {
    height: 100vh;
    text-align: center;
}

@media screen and (max-width: 480px) {
    .card_decks {
        display: grid;
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 800px) {
    .card_decks {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}
