.nav {
    background: #f4f4f4 !important;
}


.scroll {
    position: fixed;
    cursor: pointer;
    bottom: 12px;
    left: 50%;
    width: 40px;
    height: 40px;
    background: var(--primary-color);
    color: #fff;
    z-index: 1;
}

.icon {
    margin-top: 10px;
    -webkit-animation: scrollToTheTop 0.5s alternate ease infinite;
            animation: scrollToTheTop 0.5s alternate ease infinite; 
}

@-webkit-keyframes scrollToTheTop {
    from {
        transform: translateY(2px);
    }
    to {
        transform: translateY(-1px);
    }
}

@keyframes scrollToTheTop {
    from {
        transform: translateY(2px);
    }
    to {
        transform: translateY(-1px);
    }
}
